body,
html {
  height: 100%;
  min-height: 100%;
  margin: 0;
  padding: 0;
}
hr {
  border-width: 0;
  height: 1px;
  background-color: #ddd;
  margin: 15px 0;
}
md-content {
  color: #555;
}
.md-subheader h2 {
  font-size: 2em;
}
.inherit-line-height {
  line-height: inherit !important;
}
a,
a:visited,
a:hover {
  color: #37474F;
  text-decoration: none;
}
a:hover {
  text-decoration: underline;
}
.middot {
  font-weight: bold;
  padding-left: 10px;
  padding-right: 10px;
}
#app-view {
  position: relative;
  min-height: 100%;
  padding-top: 64px;
  padding-bottom: 56px;
}
#preloader {
  animation-duration: 1s;
  animation-timing-function: linear;
}
.toolbar-fixed {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 61;
}
.footer-fixed {
  color: #777;
  background-color: #fafafa;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  height: 56px;
  z-index: 61;
}
.app-header-title {
  font-size: 150%;
  font-weight: lighter;
}
.app-header-title .icon {
  font-size: 150%;
}
.app-header-title .v75 {
  font-size: 120%;
}
.app-header-title .spacy {
  letter-spacing: 5px;
}
#logo-icon {
  animation-delay: 2s;
}
.no-margin {
  margin: 0 !important;
}
.issue-list-wrapper md-list {
  padding: 0;
}
.issue-list-wrapper a.md-button {
  margin: 0;
  line-height: 140%;
}
.filter-list md-select-value {
  min-width: 100px;
}
.filter-list md-input-container {
  padding-right: 10px;
}
md-chips.no-shadow md-chips-wrap {
  box-shadow: inherit;
}
md-card.card-sm {
  max-width: 300px;
  min-width: 300px;
  width: 300px;
}
md-card.card-md {
  max-width: 500px;
  min-width: 500px;
  width: 500px;
}
md-card.card-lg {
  max-width: 650px;
  min-width: 650px;
  width: 650px;
}
.auth-card-wrapper {
  max-width: 500px;
  margin-left: auto;
  margin-right: auto;
}
.auth-card-wrapper-sm {
  max-width: 350px;
  margin-left: auto;
  margin-right: auto;
}
md-toolbar,
footer {
  animation-duration: 1s;
  animation-delay: 1s;
}
.animate-fade.ng-enter {
  position: relative;
  animation: fadeIn 1s;
}
md-input-container.no-m {
  margin: 0;
  margin-top: 10px;
}
md-input-container.m-sm {
  margin: 10px 0;
  margin-top: 20px;
}
.md-button.button-link {
  min-width: auto !important;
}
/*----------
  Account
-----------*/
md-sidenav {
  min-width: 250px;
  max-width: 250px;
  width: 250px;
}
md-sidenav.proj-list {
  overflow-y: auto;
}
md-sidenav:not(.md-locked-open) {
  padding-top: 64px;
}
.md-sidenav-menu .menu-heading {
  color: #37474F;
}
.md-sidenav-menu .md-button.active,
.md-sidenav-menu .md-button:hover,
.md-sidenav-menu .md-button:focus {
  color: white;
  background: #37474F;
}
@media only screen and (max-width: 959px) and (min-width: 0) and (orientation: portrait) {
  md-sidenav:not(.md-locked-open) {
    padding-top: 56px;
  }
  #app-view {
    padding-top: 56px;
  }
}
@media only screen and (max-width: 959px) and (min-width: 0) and (orientation: landscape) {
  md-sidenav:not(.md-locked-open) {
    padding-top: 48px;
  }
  #app-view {
    padding-top: 48px;
  }
}
@media (max-width: 959px) {
  md-card.card-lg,
  md-card.card-md,
  md-card.card-sm {
    max-width: auto;
    min-width: auto;
    width: 97%;
  }
}
@media (max-width: 599px) {
  .content-padding {
    padding-left: 0;
    padding-right: 0;
  }
  .content-padding-h {
    padding-left: 0;
    padding-right: 0;
  }
  md-card.card-lg,
  md-card.card-md,
  md-card.card-sm {
    max-width: auto;
    min-width: auto;
    width: 97%;
  }
  .filter-list md-select-value {
    min-width: 80px;
  }
  .md-subheader h2 {
    font-size: 1.5em;
  }
}
